import React, { ReactNode, HTMLAttributes } from 'react';
import PropTypes from 'prop-types';
import cx from 'classnames';
import ButtonBase from '../ButtonBase';
import omitProps from '../utils/omitProps';
import s from './styles/index.scss';
import { ChildComponentDescriptor } from '../typings/react';

// themes can be an alias to a color
// or a unique button type
export const presetThemes: { [i: string]: string } = {
  primary: 'red',
  secondary: 'mint',
  platform: 'greyblue',
  lantern: 'darkmint',
};

const propTypes = {
  theme: PropTypes.string,
  size: PropTypes.oneOf(['large', 'small']),
  outline: PropTypes.bool,
  underline: PropTypes.bool,
  link: PropTypes.bool,
  caps: PropTypes.bool,
  go: PropTypes.bool,
  children: PropTypes.node,
  block: PropTypes.bool,
  className: PropTypes.string,
  round: PropTypes.bool,
  square: PropTypes.bool,
  flat: PropTypes.bool,
  fitText: PropTypes.bool,
  onClick: PropTypes.func,
};

export type ButtonProps = HTMLAttributes<HTMLButtonElement> & {
  /**
   * Component type to wrap children with.
   */
  as?: ChildComponentDescriptor;
  /**
   * @remarks We would love to properly type this with generics, but cannot yet.
   * @see https://github.com/Codecademy/client-modules/pull/270#discussion_r270917147
   * @see https://github.com/Microsoft/TypeScript/issues/21048
   */
  asProps?: any;
  block?: boolean;
  caps?: boolean;
  children: ReactNode;
  disabled?: boolean;
  fitText?: boolean;
  flat?: boolean;
  go?: boolean;
  href?: string;
  id?: string;
  link?: boolean;
  outline?: boolean;
  rel?: string;
  round?: boolean;
  size?: 'small' | 'large';
  square?: boolean;
  target?: string;
  theme?: string;
  type?: string;
  underline?: boolean;
};

export const Button = (props: ButtonProps) => {
  let { theme = 'primary' } = props;

  if (theme && presetThemes[theme]) {
    theme = presetThemes[theme];
  }

  const typeClassName = props.link ? s.link : s.btn;
  const themeClassName = props.link ? s[`link-${theme}`] : s[`btn-${theme}`];

  const classes = cx(
    typeClassName,
    themeClassName,
    s[props.size],
    {
      [s.block]: props.block,
      [s.go]: props.go,
      [s.outline]: props.outline,
      [s.underline]: props.underline,
      [s.caps]: props.caps,
      [s.round]: props.round,
      [s.square]: props.square,
      [s.flat]: props.flat,
      [s['fit-text']]: props.fitText,
    },
    props.className
  );

  const propsToTransfer = omitProps(propTypes, props);

  return (
    <ButtonBase
      {...propsToTransfer}
      className={classes}
      link={props.link}
      onClick={props.onClick}
    >
      {props.children}
    </ButtonBase>
  );
};

Button.propTypes = propTypes;

export default Button;
