import React from 'react';
import cx from 'classnames';
import moment from 'moment';
import Truncate from 'react-truncate';
import { Notification } from './typings';
import NotificationIcon from './NotificationIcon';
import s from './styles/Notification.scss';

export type NotificationItemProps = {
  onClick?: (event: object) => void;
  notification: Notification;
};

const formatTime = (notificationDate: string): string => {
  return moment(notificationDate).fromNow();
};

export const NotificationItem = (props: NotificationItemProps) => {
  const { notification, onClick } = props;
  const {
    date,
    iconSettings,
    iconSlug,
    imageUrl,
    link,
    text,
    unread,
  } = notification;

  const notificationClasses = cx(s.notification, {
    [s.unread]: unread,
  });

  const TagName = link ? 'a' : 'div';
  const tagProps = link ? { target: '_blank' } : { role: 'presentation' };

  return (
    <TagName
      href={link}
      rel="noopener noreferrer"
      className={cx(notificationClasses)}
      onClick={onClick}
      {...tagProps}
    >
      <NotificationIcon
        iconSettings={iconSettings}
        iconSlug={iconSlug}
        imageUrl={imageUrl}
      />
      <div className={s.body}>
        <div className={s.text}>
          <Truncate lines={3} ellipsis="...">
            {text}
          </Truncate>
        </div>
        <div className={s.time}>{formatTime(date)}</div>
      </div>
    </TagName>
  );
};

export default NotificationItem;
try {
    // @ts-ignore
    NotificationItem.displayName = "NotificationItem";
    // @ts-ignore
    NotificationItem.__docgenInfo = { "description": "", "displayName": "NotificationItem", "props": { "onClick": { "defaultValue": null, "description": "", "name": "onClick", "required": false, "type": { "name": "(event: object) => void" } }, "notification": { "defaultValue": null, "description": "", "name": "notification", "required": true, "type": { "name": "Notification" } } } };
    // @ts-ignore
    if (typeof STORYBOOK_REACT_CLASSES !== "undefined")
        // @ts-ignore
        STORYBOOK_REACT_CLASSES["../../packages/gamut/src/NotificationList/NotificationItem.tsx#NotificationItem"] = { docgenInfo: NotificationItem.__docgenInfo, name: "NotificationItem", path: "../../packages/gamut/src/NotificationList/NotificationItem.tsx#NotificationItem" };
}
catch (__react_docgen_typescript_loader_error) { }