export const customElementOverrides = addSourceDecorator(() => (
  <Markdown
    text={text(
      'markdown',
      `
## Hello World

This is a custom markdown component

<CustomElement title="A Custom Component" />
    `
    )}
    theme={select('theme', ['tight', 'loose', 'none'])}
    overrides={{
      CodeBlock: {
        component: props => <span style={{ color: 'blue' }} {...props} />,
        props: {
          style: {
            color: 'red',
          },
        },
      },
      CustomElement: {
        component: ({ title }) => {
          return (
            <h3
              style={{
                padding: 10,
                background: 'rebeccapurple',
                color: 'white',
                borderRadius: 5,
              }}
            >
              {title}
            </h3>
          );
        },
        allowedAttributes: ['title'],
      },
    }}
  />
), {__STORY__, __ADDS_MAP__,__MAIN_FILE_LOCATION__,__MODULE_DEPENDENCIES__,__LOCAL_DEPENDENCIES__,__SOURCE_PREFIX__,__IDS_TO_FRAMEWORKS__});;

customElementOverrides.story = {
  name: 'Custom Element Overrides',

  parameters: {
    knobs: {
      escapeHTML: false,
    },
  },
};

