import cx from 'classnames';
import React, { HTMLAttributes } from 'react';

import styles from './styles.scss';

export type HighlightedTextProps = {
  children?: string;
  wordProps?: HTMLAttributes<HTMLSpanElement>;
  textProps?: HTMLAttributes<HTMLElement>;
};

export const HighlightedText: React.FC<HighlightedTextProps> = ({
  children = '',
  textProps = {},
}) => {
  const combinedProps = {
    ...textProps,
    className: cx(styles.highlightedText, textProps.className),
  };

  const words = children
    .split(/(\s+)(\S+)/g)
    .filter(Boolean)
    .map((word, i) => (
      // eslint-disable-next-line react/no-array-index-key
      <span className={styles.word} key={i}>
        {word}
      </span>
    ));

  return <em {...combinedProps}>{words}</em>;
};

export default HighlightedText;
try {
    // @ts-ignore
    HighlightedText.displayName = "HighlightedText";
    // @ts-ignore
    HighlightedText.__docgenInfo = { "description": "", "displayName": "HighlightedText", "props": { "wordProps": { "defaultValue": null, "description": "", "name": "wordProps", "required": false, "type": { "name": "HTMLAttributes<HTMLSpanElement>" } }, "textProps": { "defaultValue": { value: "{}" }, "description": "", "name": "textProps", "required": false, "type": { "name": "HTMLAttributes<HTMLElement>" } } } };
    // @ts-ignore
    if (typeof STORYBOOK_REACT_CLASSES !== "undefined")
        // @ts-ignore
        STORYBOOK_REACT_CLASSES["../../packages/gamut/src/HighlightedText/index.tsx#HighlightedText"] = { docgenInfo: HighlightedText.__docgenInfo, name: "HighlightedText", path: "../../packages/gamut/src/HighlightedText/index.tsx#HighlightedText" };
}
catch (__react_docgen_typescript_loader_error) { }