FwB,CE2FxB,iBCzGyD,CD0GzD,mBFxGoB,CEyGpB,eC3GyD,CJmD3D,+DG2DI,WAAY,CACZ,YAAa,CHjDjB,+BG0CE,qBF9FwB,CE+FxB,cCzGyD,CD0GzD,eFvGsB,CEwGtB,cC3GyD,CJ8D3D,+DGgDI,WAAY,CACZ,YAAa,CHlCjB,+BACE,YAAa,CACb,UAAW,CAGb,8BACE,wBAAyB,CAG3B,gFAGI,yBAA0B,CAK9B,kJAII,UAAW,CAMf,yDACE,GACE,oCKoM2B,CLlM7B,GACE,oCKiM2B,CAAA,CLtM/B,iDACE,GACE,oCKoM2B,CLlM7B,GACE,oCKiM2B,CAAA,CL7L/B,iEAGI,oCK0L2B,CLzL3B,wEAAyC,CAAzC,gEAAyC,CACzC,qCAA8B,CAA9B,6BAA8B,CALlC,uEAQM,sBAAe,CAAf,cAAe","file":"index.scss","sourcesContent":["@import \"variables\";\n@import \"mixins\";\n\n//\n// Base styles\n//\n\n.btn {\n  display: inline-flex;\n  justify-content: center;\n  font-weight: $btn-font-weight;\n  @include font-smoothing;\n  border: 1px solid transparent;\n  border-radius: $btn-border-radius;\n  user-select: none;\n  @include button-size(\n    $btn-padding-y,\n    $btn-padding-x,\n    $btn-font-size-base,\n    $btn-line-height,\n    $btn-min-width-sm\n  );\n  transition: all 0.1s ease-in-out;\n}\n\n// Future-proof disabling of clicks on `<a>` elements\na.btn.disabled,\nfieldset[disabled] a.btn {\n  pointer-events: none;\n}\n\n@each $name, $color in $btn-swatches {\n  @if $name == \"yellow\" {\n    @include button-variants($name, $color-black, $color);\n  }\n  @elseif $name == \"brand-yellow\" {\n    @include button-variants($name, $color-black, $color);\n  }\n  @elseif lightness($color) > 68 {\n    @include button-variants($name, $color-black, $color);\n  } @else {\n    @include button-variants($name, $color-white, $color);\n  }\n}\n\n.round {\n  border-radius: $btn-round-border-radius;\n}\n\n.square {\n  border-radius: 0;\n}\n//\n// Button Sizes\n//\n\n.large {\n  // line-height: ensure even-numbered height of button next to large input\n  @include button-size(\n    $btn-padding-y-lg,\n    $btn-padding-x-lg,\n    $btn-font-size-lg,\n    $btn-line-height-lg,\n    $btn-min-width-lg\n  );\n}\n\n.small {\n  // line-height: ensure proper height of button next to small input\n  @include button-size(\n    $btn-padding-y-sm,\n    $btn-padding-x-sm,\n    $btn-font-size-sm,\n    $btn-line-height-sm,\n    $btn-min-width-sm\n  );\n}\n\n//\n// Block button\n//\n\n.block {\n  display: flex;\n  width: 100%;\n}\n\n.caps {\n  text-transform: uppercase;\n}\n\n.underline {\n  &:hover,\n  &:focus {\n    text-decoration: underline;\n  }\n}\n\n// Specificity overrides\ninput[type=\"submit\"],\ninput[type=\"reset\"],\ninput[type=\"button\"] {\n  &.block {\n    width: 100%;\n  }\n}\n\n// \"go\" animation styles\n\n@keyframes lantern-pulse {\n  0% {\n    background-color: rgba($deprecated-color-mint, 0.2);\n  }\n  100% {\n    background-color: rgba($deprecated-color-mint, 0.5);\n  }\n}\n\n.go {\n  // lantern\n  &.btn-darkmint {\n    background-color: rgba($deprecated-color-mint, 0.5);\n    animation: lantern-pulse 2s infinite ease;\n    animation-direction: alternate;\n\n    &:hover {\n      animation: none;\n    }\n  }\n}\n","@import \"~@codecademy/gamut-styles/utils\";\n\n$btn-padding-x: px-rem(16) !default;\n$btn-padding-y: 0.375rem !default;\n$btn-font-weight: bold !default;\n\n$btn-line-height: 1.5 !default;\n$btn-line-height-lg: 4 / 3 !default;\n$btn-line-height-sm: 1.5 !default;\n\n$btn-font-size-base: px-rem(16) !default;\n$btn-font-size-lg: px-rem(20) !default;\n$btn-font-size-sm: px-rem(16) !default;\n$btn-font-size-xs: 0.75rem !default;\n\n$btn-padding-x-sm: 0.75rem !default;\n$btn-padding-y-sm: 0.25rem !default;\n$btn-min-width-sm: px-rem(128) !default;\n\n$btn-padding-x-lg: 1.25rem !default;\n$btn-padding-y-lg: 0.75rem !default;\n$btn-min-width-lg: px-rem(160) !default;\n\n$btn-border-radius: 2px !default;\n$btn-round-border-radius: 50px !default;\n\n$btn-state-modifier: 20% !default;\n$btn-color-modifier: 10% !default;\n$btn-outline-hover-state-modifier: 0.9 !default;\n$btn-outline-active-state-modifier: 0.6 !default;\n$btn-box-shadow-focus-modifier: 0.5 !default;\n\n$btn-disabled-color: $deprecated-gamut-grey-800;\n$btn-box-shadow-color: rgba(0, 0, 0, 0.3);\n\n$btn-swatches: (\n  \"mint\": $deprecated-swatches-mint-700,\n  \"darkmint\": $deprecated-swatches-mint-800,\n  \"blue\": $deprecated-gamut-blue-500,\n  \"darkblue\": $deprecated-swatches-cc-blue-500,\n  \"midnightblue\": $deprecated-swatches-grey-blue-500,\n  \"grey\": $deprecated-swatches-grey-300,\n  \"red\": $deprecated-swatches-red-500,\n  \"yellow\": $deprecated-gamut-yellow-500,\n  \"greyblue\": $deprecated-swatches-grey-blue-600,\n  \"white\": $deprecated-color-white,\n  \"ccblue\": $deprecated-swatches-cc-blue-700,\n  \"royalblue\": $deprecated-gamut-royal-blue,\n  \"purple\": $deprecated-gamut-purple,\n  \"brand-red\": $brand-red,\n  \"brand-yellow\": $brand-yellow,\n  \"brand-purple\": $brand-purple,\n  \"brand-pink\": $brand-pink,\n  \"brand-mint\": $brand-mint,\n  \"brand-beige\": $brand-beige,\n  \"brand-dark-blue\": $brand-dark-blue,\n  \"brand-blue\": $color-blue-500\n);\n","@mixin font-smoothing($value: on) {\n  @if $value == on {\n    -webkit-font-smoothing: antialiased;\n    -moz-osx-font-smoothing: grayscale;\n  } @else {\n    -webkit-font-smoothing: subpixel-antialiased;\n    -moz-osx-font-smoothing: auto;\n  }\n}\n","// Button variants\n//\n// Easily pump out default styles, as well as :hover, :focus, :active,\n// and disabled options for all buttons\n\n@mixin button-variant($color, $background, $border: transparent) {\n  $active-background: mix($color-black, $background, $btn-state-modifier);\n\n  @if $border == transparent {\n    $active-border: transparent;\n    $active-border-hover: transparent;\n  }\n\n  color: $color;\n  background-color: $background;\n  border-color: $border;\n\n  &:hover {\n    box-shadow: 0 2px 4px $btn-box-shadow-color;\n  }\n\n  &:focus {\n    box-shadow: 0 0 0 2px $color-white, 0 0 0 4px $background;\n  }\n\n  &:focus,\n  &:hover {\n    text-decoration: none;\n    color: $color;\n\n    &:active {\n      box-shadow: 0 2px 4px $btn-box-shadow-color;\n    }\n  }\n\n  &:active {\n    background-color: $active-background;\n  }\n\n  &:disabled {\n    background-color: $btn-disabled-color;\n\n    &:hover {\n      box-shadow: none;\n    }\n  }\n}\n\n@mixin button-outline-variant($color) {\n  $active-contrast-base: text-contrast-color($color);\n  $active-background: mix($active-contrast-base, $color, $btn-state-modifier);\n  color: $color;\n  background-color: transparent;\n  border: 1px solid $color;\n\n  &:focus,\n  &:hover {\n    background-color: $color;\n    box-shadow: none;\n    color: $active-contrast-base;\n\n    &:active {\n      background-color: $active-background;\n      box-shadow: none;\n    }\n  }\n\n  &:hover {\n    box-shadow: 0 2px 4px 0 $btn-box-shadow-color;\n  }\n\n  &:focus {\n    box-shadow: 0 0 0 2px $color-white, 0 0 0 4px $color;\n  }\n\n  &:disabled {\n    border-color: $btn-disabled-color;\n    color: $btn-disabled-color;\n    background-color: transparent;\n  }\n}\n\n@mixin button-flat-variant($color) {\n  color: $color;\n  background-color: transparent;\n\n  &:hover,\n  &:active {\n    box-shadow: none;\n  }\n\n  &:focus {\n    box-shadow: 0 0 0 2px $color-white, 0 0 0 4px $color;\n  }\n\n  &:disabled {\n    color: $btn-disabled-color;\n    background-color: transparent;\n  }\n}\n\n// Button sizes\n@mixin button-size(\n  $padding-y,\n  $padding-x,\n  $font-size,\n  $line-height,\n  $min-width\n) {\n  padding: $padding-y $padding-x;\n  font-size: $font-size;\n  line-height: $line-height;\n  min-width: $min-width;\n\n  &.fit-text {\n    min-width: 0;\n    min-height: 0;\n  }\n}\n\n@mixin button-variants($name, $color, $background, $border: transparent) {\n  .btn-#{$name} {\n    @include button-variant($color, $background, $border);\n    &.outline {\n      @include button-outline-variant($background);\n    }\n    &.flat {\n      @include button-flat-variant($background);\n    }\n    @content;\n  }\n  .link-#{$name} {\n    font-weight: bold;\n    @include font-smoothing;\n    color: $background;\n    text-decoration: underline;\n\n    &:disabled {\n      color: $btn-disabled-color;\n    }\n  }\n}\n\n// Shamelessly copied from https://codepen.io/davidhalford/pen/wlDxL\n// Adds text color to an element based on what has more contrast with its background\n\n@function color-brightness($color) {\n  @return round(\n    (red($color) * 299) + (green($color) * 587) + (blue($color) * 114) / 1000\n  );\n}\n\n$brightness-threshold: color-brightness($color-white) / 2;\n\n@function text-contrast-color($background) {\n  $brightness: color-brightness($background);\n\n  @if abs($brightness) < $brightness-threshold {\n    @return #fff;\n  }\n\n  @return #000;\n}\n","@function strip-units($number) {\n  @return $number / ($number * 0 + 1);\n}\n\n@function px-rem($px, $base-px: 16) {\n  @return (strip-units($px) / strip-units($base-px)) * 1rem;\n}\n\n@function px-em($px, $base-px: 16) {\n  @return (strip-units($px) / strip-units($base-px)) * 1em;\n}\n\n// Math\n@function pow($number, $exponent) {\n  $value: 1;\n\n  @if $exponent > 0 {\n    @for $i from 1 through $exponent {\n      $value: $value * $number;\n    }\n  }\n\n  @return $value;\n}\n","// NOTE: When updating, make sure you also update the gamut-styles/variables/index.js file\n\n// =======================================\n//      COLORS\n// =======================================\n\n$color-blue-100: #c8d7fa;\n$color-blue-200: #a5befa;\n$color-blue-300: #7da2fa;\n$color-blue-400: #5788fa;\n$color-blue-500: #3069f0;\n$color-blue-600: #2d5dcc;\n$color-blue-700: #2e4a99;\n$color-blue-800: #233466;\n$color-blue-900: #141e3b;\n$color-blue-1000: #10162f;\n$color-blue-1100: #0a0e1d;\n\n$color-pink-100: #ffd9fc;\n$color-pink-200: #ffbffa;\n$color-pink-300: #ffa6f8;\n$color-pink-400: #f288e9;\n$color-pink-500: #d957d9;\n$color-pink-600: #b035c9;\n$color-pink-700: #9129a6;\n$color-pink-800: #702080;\n$color-pink-900: #43134d;\n\n$color-purple-100: #d5ccff;\n$color-purple-200: #c0b6f2;\n$color-purple-300: #ac9df2;\n$color-purple-400: #917ef2;\n$color-purple-500: #7c5ce6;\n$color-purple-600: #6437cc;\n$color-purple-700: #4b2999;\n$color-purple-800: #381f73;\n$color-purple-900: #231347;\n\n$color-red-100: #ffd3cc;\n$color-red-200: #ffb8ad;\n$color-red-300: #ff988c;\n$color-red-400: #ff7566;\n$color-red-500: #fd4d3f;\n$color-red-600: #e53935;\n$color-red-700: #bf2e2c;\n$color-red-800: #992523;\n$color-red-900: #661917;\n\n$color-green-100: #bbfae5;\n$color-green-200: #91f2d2;\n$color-green-300: #6aebc0;\n$color-green-400: #4fe0b0;\n$color-green-500: #47cca0;\n$color-green-600: #3eb38c;\n$color-green-700: #318c6e;\n$color-green-800: #246650;\n$color-green-900: #164032;\n\n$color-orange-100: #ffe9c8;\n$color-orange-200: #ffd093;\n$color-orange-300: #ffb764;\n$color-orange-400: #ff9f3c;\n$color-orange-500: #ff881d;\n$color-orange-600: #fb7106;\n$color-orange-700: #dc5a03;\n$color-orange-800: #ba4604;\n$color-orange-900: #963606;\n\n$color-yellow-100: #fff7cc;\n$color-yellow-200: #fff2b3;\n$color-yellow-300: #ffec8c;\n$color-yellow-400: #ffe359;\n$color-yellow-500: #ffd500;\n$color-yellow-600: #ffb92e;\n$color-yellow-700: #e69729;\n$color-yellow-800: #b37620;\n$color-yellow-900: #805417;\n\n$color-gray-100: #f6f5fa;\n$color-gray-200: #dddce0;\n$color-gray-300: #c4c3c7;\n$color-gray-400: #a2a2a6;\n$color-gray-500: #828285;\n$color-gray-600: #646466;\n$color-gray-700: #4b4b4d;\n$color-gray-800: #323233;\n$color-gray-900: #19191a;\n\n$color-white: #ffffff;\n$color-black: #000000;\n$color-beige: #efd9ca;\n$color-royal-blue: #6400e4;\n$color-shadow: rgba($color-black, 0.15);\n\n// =======================================\n//      BRAND COLORS\n// =======================================\n\n$brand-red: #fd4d3f;\n$brand-yellow: #ffc740;\n$brand-purple: #6400e4;\n$brand-pink: #ff8ef5;\n$brand-mint: #6df0c2;\n$brand-beige: #efd9ca;\n$brand-dark-blue: #141c3a;\n\n// =======================================\n//      DEPRECATED COLORS\n// =======================================\n\n$deprecated-color-white: #ffffff;\n$deprecated-color-black: #000000;\n\n$deprecated-gamut-purple-500: #69639a;\n\n$deprecated-gamut-purple-100: mix(\n  $color-white,\n  $deprecated-gamut-purple-500,\n  80%\n);\n$deprecated-gamut-purple-200: mix(\n  $color-white,\n  $deprecated-gamut-purple-500,\n  60%\n);\n$deprecated-gamut-purple-300: mix(\n  $color-white,\n  $deprecated-gamut-purple-500,\n  40%\n);\n$deprecated-gamut-purple-400: mix(\n  $color-white,\n  $deprecated-gamut-purple-500,\n  20%\n);\n$deprecated-gamut-purple-600: mix(\n  $color-black,\n  $deprecated-gamut-purple-500,\n  20%\n);\n$deprecated-gamut-purple-700: mix(\n  $color-black,\n  $deprecated-gamut-purple-500,\n  40%\n);\n$deprecated-gamut-purple-800: mix(\n  $color-black,\n  $deprecated-gamut-purple-500,\n  60%\n);\n$deprecated-gamut-purple-900: mix(\n  $color-black,\n  $deprecated-gamut-purple-500,\n  80%\n);\n\n$deprecated-gamut-royal-blue-500: #4b35ef;\n\n$deprecated-gamut-royal-blue-100: mix(\n  $color-white,\n  $deprecated-gamut-royal-blue-500,\n  80%\n);\n$deprecated-gamut-royal-blue-200: mix(\n  $color-white,\n  $deprecated-gamut-royal-blue-500,\n  60%\n);\n$deprecated-gamut-royal-blue-300: mix(\n  $color-white,\n  $deprecated-gamut-royal-blue-500,\n  40%\n);\n$deprecated-gamut-royal-blue-400: mix(\n  $color-white,\n  $deprecated-gamut-royal-blue-500,\n  20%\n);\n$deprecated-gamut-royal-blue-600: mix(\n  $color-black,\n  $deprecated-gamut-royal-blue-500,\n  20%\n);\n$deprecated-gamut-royal-blue-700: mix(\n  $color-black,\n  $deprecated-gamut-royal-blue-500,\n  40%\n);\n$deprecated-gamut-royal-blue-800: mix(\n  $color-black,\n  $deprecated-gamut-royal-blue-500,\n  60%\n);\n$deprecated-gamut-royal-blue-900: mix(\n  $color-black,\n  $deprecated-gamut-royal-blue-500,\n  80%\n);\n\n$deprecated-gamut-blue-500: #4c7ef3;\n\n$deprecated-gamut-blue-100: mix($color-white, $deprecated-gamut-blue-500, 80%);\n$deprecated-gamut-blue-200: mix($color-white, $deprecated-gamut-blue-500, 60%);\n$deprecated-gamut-blue-300: mix($color-white, $deprecated-gamut-blue-500, 40%);\n$deprecated-gamut-blue-400: mix($color-white, $dep